export default [
  {
    path:'/Detail/:skuId',
    meta: {
      title: ''
    },
    component: () => import('./detail.vue')
  },
  {
    path:'/Detail/:skuId/:packageId/:spaceId',
    meta: {
      title: ''
    },
    component: () => import('./detail.vue')
  },
  {
    path:'/ComboDetail/:pid',
    meta: {
      title: ''
    },
    component: () => import('./comboDetail.vue')
  }
]



