export default [
  {
    name: '',
    component: () => import('./index.vue')
  },
  {
    name: '',
    path: '/detail/:orderId',
    component: () => import('./detail.vue')
  },
  {
    meta: {
      title: ''
    },
    path: '/express/:Id',
    component: () => import('./express.vue')
  }
]



